@import "../../styles/abstracts";

.team {
	padding: 45px var(--section-horizontal-padding) 60px;
	background-color: var(--black);
	color: var(--white);

	@media screen and (min-width: #{$screen-medium}) {
		padding: 120px var(--section-horizontal-padding) 60px;
	}

	@media screen and (min-width: #{$screen-large}) {
		padding: 160px var(--section-horizontal-padding) 80px;
	}
}

.members {
	display: grid;
	grid-template-columns: repeat(1, 1fr);
	grid-column-gap: 20px;
	grid-row-gap: 20px;
	align-items: start;

	@media screen and (min-width: #{$screen-large}) {
		grid-template-columns: repeat(4, 1fr);
		grid-row-gap: 58px;
	}
}
