@import "./abstracts";

html,
body {
	padding: 0;
	margin: 0;
	font-family: Helvetica, sans-serif, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
		Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue;
}

body {
	background-color: var(--grey);
	color: var(--black);
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	-webkit-tap-highlight-color: transparent;

	&:before,
	&:after {
		box-sizing: border-box;
		-webkit-tap-highlight-color: transparent;
	}
}

main {
	position: relative;
	z-index: 1;
	background: var(--black);
}

a {
	color: inherit;
	text-decoration: none;
	font-weight: 300;
}

img {
	max-width: 100%;
}

p {
	font-weight: 300;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 300;
}

:root {
	--black: #000000;
	--white: #ffffff;
	--grey: #e1e1e1;

	--section-horizontal-padding: 16px;

	--header-height: 45px;

	--md-text: 22px;
	--lg-text: 48px;
	--md-title: 18px;
	--lg-title: 26px;
	--section-title: 18px;

	@media screen and (min-width: #{$screen-medium}) {
		--header-height: 120px;

		--md-text: 26px;
		--lg-text: 72px;
		--md-title: 26px;
		--lg-title: 42px;
		--section-title: 54px;
	}

	@media screen and (min-width: #{$screen-large}) {
		--section-horizontal-padding: 40px;

		--header-height: 160px;

		--md-text: 32px;
		--lg-text: 80px;
		--md-title: 32px;
		--lg-title: 64px;
		--section-title: 72px;
	}

	@media screen and (min-width: #{$screen-extra-large}) {
		--lg-title: 72px;
	}
}
